// API Route: /api/v1/chatbot/customization/[subAccountId]
// Handles chatbot customization settings (GET, PUT)

import { NextRequest, NextResponse } from 'next/server';
import { supabaseAdmin } from '@/lib/supabase/server';

// GET: Fetch chatbot customization settings for a sub-account
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ subAccountId: string }> }
) {
  try {
    const { subAccountId } = await params;

    if (!subAccountId) {
      return NextResponse.json(
        { error: 'Sub-account ID is required' },
        { status: 400 }
      );
    }

    const supabase = supabaseAdmin;

    // Fetch customization settings
    const { data: customization, error: customizationError } = await supabase
      .from('ChatbotCustomization')
      .select('*')
      .eq('subAccountId', subAccountId)
      .single();

    if (customizationError && customizationError.code !== 'PGRST116') {
      throw customizationError;
    }

    // If no customization exists, create default one
    if (!customization) {
      const defaultCustomization = {
        id: `custom_${Date.now()}_${Math.random().toString(36).substring(7)}`,
        subAccountId,
        subscriptionActive: false,
        subscriptionTier: 'free',
        enabled: true,
        position: 'bottom-right',
        theme: 'light',
        primaryColor: '#3b82f6',
        secondaryColor: '#10b981',
        backgroundColor: '#ffffff',
        textColor: '#1f2937',
        botMessageColor: '#f3f4f6',
        userMessageColor: '#3b82f6',
        avatarType: 'default',
        avatarEmoji: 'ðŸ¤–',
        showLogo: true,
        chatTitle: 'Chat with us',
        welcomeMessage: 'Hi! How can I help you today?',
        placeholderText: 'Type your message...',
        buttonText: 'Start Chat',
        showTypingIndicator: true,
        showReadReceipts: false,
        enableFileUpload: false,
        enableVoiceInput: false,
        enableEmojiPicker: false,
        enableCannedResponses: false,
        widgetSize: 'medium',
        expandedWidth: 400,
        expandedHeight: 600,
        autoOpen: false,
        autoOpenDelay: 5,
        soundEnabled: true,
        showPoweredBy: true,
        businessHoursEnabled: false,
        outsideBusinessHoursMessage: 'We are currently offline. Leave us a message!',
      };

      const { data: newCustomization, error: createError } = await supabase
        .from('ChatbotCustomization')
        .insert([defaultCustomization])
        .select()
        .single();

      if (createError) throw createError;

      return NextResponse.json({
        success: true,
        data: newCustomization,
      });
    }

    // Fetch subscription status
    const { data: subscription } = await supabase
      .from('ChatbotSubscriptionStatus')
      .select('*')
      .eq('subAccountId', subAccountId)
      .single();

    return NextResponse.json({
      success: true,
      data: {
        ...customization,
        subscription: subscription || null,
      },
    });
  } catch (error: any) {
    console.error('Error fetching chatbot customization:', error);
    return NextResponse.json(
      { 
        success: false, 
        error: error.message || 'Failed to fetch customization settings' 
      },
      { status: 500 }
    );
  }
}

// PUT: Update chatbot customization settings
export async function PUT(
  request: NextRequest,
  { params }: { params: Promise<{ subAccountId: string }> }
) {
  try {
    const { subAccountId } = await params;
    const body = await request.json();

    if (!subAccountId) {
      return NextResponse.json(
        { error: 'Sub-account ID is required' },
        { status: 400 }
      );
    }

    const supabase = supabaseAdmin;

    // Check if customization exists
    const { data: existing } = await supabase
      .from('ChatbotCustomization')
      .select('subscriptionTier')
      .eq('subAccountId', subAccountId)
      .single();

    if (!existing) {
      return NextResponse.json(
        { error: 'Customization settings not found. Please fetch first to initialize.' },
        { status: 404 }
      );
    }

    // Get subscription tier to validate feature access
    const tier = existing.subscriptionTier || 'free';

    // Validate feature access based on tier
    const restrictedFeatures = validateFeatureAccess(tier, body);
    
    if (restrictedFeatures.length > 0) {
      return NextResponse.json(
        {
          success: false,
          error: 'Some features require a subscription upgrade',
          restrictedFeatures,
          currentTier: tier,
          upgradeUrl: `/settings/chatbot-customization/upgrade`,
        },
        { status: 403 }
      );
    }

    // Update customization settings
    const { data: updated, error: updateError } = await supabase
      .from('ChatbotCustomization')
      .update({
        ...body,
        updatedAt: new Date().toISOString(),
      })
      .eq('subAccountId', subAccountId)
      .select()
      .single();

    if (updateError) throw updateError;

    return NextResponse.json({
      success: true,
      data: updated,
      message: 'Customization settings updated successfully',
    });
  } catch (error: any) {
    console.error('Error updating chatbot customization:', error);
    return NextResponse.json(
      {
        success: false,
        error: error.message || 'Failed to update customization settings',
      },
      { status: 500 }
    );
  }
}

// Helper function to validate feature access based on subscription tier
function validateFeatureAccess(tier: string, updates: any): string[] {
  const restrictedFeatures: string[] = [];

  // Define tier capabilities
  const tierFeatures: Record<string, string[]> = {
    free: [
      'position',
      'theme',
      'primaryColor',
      'chatTitle',
      'welcomeMessage',
      'placeholderText',
      'buttonText',
    ],
    basic: [
      ...['position', 'theme', 'primaryColor', 'chatTitle', 'welcomeMessage', 'placeholderText', 'buttonText'],
      'secondaryColor',
      'backgroundColor',
      'textColor',
      'botMessageColor',
      'userMessageColor',
      'avatarType',
      'avatarUrl',
      'avatarEmoji',
      'avatarInitials',
      'showLogo',
      'logoUrl',
      'showTypingIndicator',
      'enableFileUpload',
      'widgetSize',
      'soundEnabled',
    ],
    premium: [
      ...['position', 'theme', 'primaryColor', 'secondaryColor', 'backgroundColor', 'textColor', 'botMessageColor', 'userMessageColor', 'avatarType', 'avatarUrl', 'avatarEmoji', 'avatarInitials', 'showLogo', 'logoUrl', 'showTypingIndicator', 'enableFileUpload', 'widgetSize', 'soundEnabled'],
      'showReadReceipts',
      'enableVoiceInput',
      'enableEmojiPicker',
      'enableCannedResponses',
      'businessHoursEnabled',
      'businessHours',
      'outsideBusinessHoursMessage',
      'showPoweredBy', // Can remove branding
      'expandedWidth',
      'expandedHeight',
      'autoOpen',
      'autoOpenDelay',
    ],
    enterprise: [
      // All features
      'position',
      'theme',
      'primaryColor',
      'secondaryColor',
      'backgroundColor',
      'textColor',
      'botMessageColor',
      'userMessageColor',
      'avatarType',
      'avatarUrl',
      'avatarEmoji',
      'avatarInitials',
      'showLogo',
      'logoUrl',
      'showTypingIndicator',
      'showReadReceipts',
      'enableFileUpload',
      'enableVoiceInput',
      'enableEmojiPicker',
      'enableCannedResponses',
      'widgetSize',
      'expandedWidth',
      'expandedHeight',
      'autoOpen',
      'autoOpenDelay',
      'soundEnabled',
      'showPoweredBy',
      'customCSS',
      'customJS',
      'businessHoursEnabled',
      'businessHours',
      'outsideBusinessHoursMessage',
    ],
  };

  const allowedFeatures = tierFeatures[tier] || tierFeatures.free;

  // Check each update field
  Object.keys(updates).forEach((key) => {
    if (!allowedFeatures.includes(key) && !['subAccountId', 'id', 'createdAt', 'updatedAt'].includes(key)) {
      restrictedFeatures.push(key);
    }
  });

  return restrictedFeatures;
}
